import React, { memo, useEffect, useRef, useState } from 'react';
import Downshift from 'downshift';
import { arrayOf, bool, func, shape, string, oneOf } from 'prop-types';
import Listbox from './listbox';
import classnames from 'classnames';
import ButtonIcon from '../button-icon';
import Icon from '../icon';
import Input from '../input';
import useScrollToItem from './useScrollToItem';
import useComboboxAction from './useComboboxAction';
import { ComboboxUtil } from '@visa/ui-utils';

const CSS_PREFIX = 'vds-combobox';

export const COMBOBOX_TYPE = {
  auto: 'auto',
  manual: 'manual',
  none: 'none'
};

const preventRerender = (prevProps, nextProps) => JSON.stringify(prevProps) === JSON.stringify(nextProps);

const Combobox = memo(({
  className,
  comboboxClass,
  containerProps,
  defaultItem,
  comboboxAction,
  errorText,
  filterFunction,
  inputProps,
  noMatchMessage,
  onInputBlur,
  onInputChange,
  onInputFocus,
  onSelectionChange,
  optionsTemplate,
  showErrorText,
  showHelpText,
  type,
  ...restProps
}) => {
  if (typeof filterFunction() !== 'boolean') {
    throw new Error('Filter function must return a boolean.');
  }

  const [currentSelectItem, setCurrentSelectItem] = useState(defaultItem ? defaultItem : null);
  const inputRef = useRef(null);
  const [inputError, setInputError] = useState(errorText);
  const [inputValue, setInputValue] = useState(defaultItem ? defaultItem.value : '');
  const [isListboxOpen, setListboxOpen] = useState(false);
  const [searchHasNoMatch, setSearchHasNoMatch] = useState(false);
  const { listboxItemRef, listboxRef } = useScrollToItem(currentSelectItem, optionsTemplate, type);

  //Reverse of Control, expose downshift internal action to parent component
  const downshiftInternalActions = {};
  const actionList = ['clearSelection', 'closeMenu', 'openMenu', 'selectItem', 'setHighlightedIndex'];
  useComboboxAction(comboboxAction, downshiftInternalActions);

  useEffect(() => {
    if (searchHasNoMatch || errorText) {
      if (searchHasNoMatch && noMatchMessage) {
        setInputError(noMatchMessage);
      } else if (errorText && errorText !== inputError) {
        setInputError(errorText);
      } else {
        setInputError(undefined);
      }
    } else {
      setInputError(undefined);
    }
    if (searchHasNoMatch) {
      setListboxOpen(false);
    } else {
      setListboxOpen(true);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [errorText, noMatchMessage, searchHasNoMatch]);

  //set default index
  let defaultIndex = -1;

  if (defaultItem && optionsTemplate && optionsTemplate.optionValues) {
    defaultIndex = optionsTemplate.optionValues.findIndex(
      item => item[optionsTemplate.itemKey] === defaultItem[optionsTemplate.itemKey]
    );
  }

  const handleInputBlur = e => {
    if (typeof onInputBlur === 'function') {
      if (currentSelectItem) {
        onInputBlur(e, currentSelectItem.value);
      } else {
        onInputBlur(e);
      }
    }
  };

  const handleInputChange = e => {
    setInputValue(e.target.value);
    if (typeof onInputChange === 'function') {
      onInputChange(e, e.target.value);
    }
  };

  const handleInputFocus = e => {
    if (inputRef.current) {
      inputRef.current.setSelectionRange(0, e.target.value.length);
    }
    if (typeof onInputFocus === 'function') {
      onInputFocus(e, e.target.value);
    }
  };

  const updateInputValue = selection => {
    if (selection) {
      setInputValue(selection.value);
    }
  };

  const handleSelection = selection => {
    updateInputValue(selection);
    if (typeof onSelectionChange === 'function') {
      onSelectionChange(selection);
    }
  };

  const handleNoMatch = noMatch => {
    if (searchHasNoMatch !== noMatch) {
      setSearchHasNoMatch(noMatch);
    }
  };

  const handleA11yStatusMessage = status => {
    if (type === COMBOBOX_TYPE.auto && status.isOpen) {
      setCurrentSelectItem(status.highlightedItem);
    }
    return status.isOpen && status.highlightedItem ? status.highlightedItem.value : '';
  };

  const handleOuterClick = () => {
    if (currentSelectItem && currentSelectItem.value !== inputValue) {
      downshiftInternalActions.selectItem(currentSelectItem);
    }
  };


  const stateReducer = (state, changes) => {
    switch (changes.type) {
      case Downshift.stateChangeTypes.changeInput:
        let changeInputChanges = { ...changes };
        if (type === COMBOBOX_TYPE.none || type === COMBOBOX_TYPE.manual) {
          if (state.selectedItem) {
            changeInputChanges = { ...changeInputChanges, selectedItem: null };
            setCurrentSelectItem(null);
          }
        } else if (type === COMBOBOX_TYPE.auto) {
          if (inputError === noMatchMessage) {
            changeInputChanges = { ...changeInputChanges, selectedItem: null };
            setCurrentSelectItem(null);
          }
          changeInputChanges = { ...changeInputChanges, highlightedIndex: 0 };
        }
        return changeInputChanges;
      case Downshift.stateChangeTypes.clickButton:
      case Downshift.stateChangeTypes.blurInput:
        let blurInputChanges = { ...changes };
        if ( type === COMBOBOX_TYPE.auto && changes.isOpen === false) {
          if (inputError === noMatchMessage) {
            blurInputChanges = { ...blurInputChanges, inputValue: state.inputValue };
          } else if (currentSelectItem !== blurInputChanges.selectedItem) {
            blurInputChanges = { ...blurInputChanges, selectedItem: currentSelectItem };
            handleSelection(currentSelectItem);
          }
        }
        return blurInputChanges;
      case Downshift.stateChangeTypes.keyDownEscape: {
        return {
          ...changes,
          selectedItem: currentSelectItem
        };
      }
      default:
        return changes;
    }
  };

  return (
    <Downshift
      defaultHighlightedIndex={defaultIndex}
      initialInputValue={defaultItem ? defaultItem.value : ''}
      inputValue={inputValue}
      itemToString={item => (item ? item.value : '')}
      onChange={handleSelection}
      stateReducer={stateReducer}
      onOuterClick={handleOuterClick}
      getA11yStatusMessage={handleA11yStatusMessage}
    >
      {({ getInputProps, getToggleButtonProps, isOpen, ...listboxDownshift }) => {
        if (Object.keys(downshiftInternalActions).length === 0) {
          downshiftInternalActions.setInputError = setInputError;
      for (const action of actionList) { // eslint-disable-line
            downshiftInternalActions[action] = listboxDownshift[action];
          }
        }
        return (
          <div
            {...containerProps}
            className={classnames(`${CSS_PREFIX}-container`, className, comboboxClass.containerClass)}
          >
            <Input
              {...getInputProps({
                onBlur: handleInputBlur,
                onChange: handleInputChange,
                onFocus: handleInputFocus
              })}
              {...inputProps}
              className={classnames(`${CSS_PREFIX}-input-wrapper`, comboboxClass.inputWrapperClass)}
              errorText={inputError}
              invalid={Boolean(inputError)}
              labelId={getInputProps()['aria-labelledby']}
              noValidate
              ref={inputRef}
              showErrorText={Boolean(inputError) && showErrorText}
              showHelpText={Boolean(inputProps.helpText) && showHelpText}
              type="text"
            >
              <ButtonIcon
                ariaLabel={isOpen ? 'collapse' : 'expand'}
                {...getToggleButtonProps({})}
                iconType="light-tiny"
                tabIndex="-1"
              >
                <Icon name={isOpen ? 'arrow-collapse' : 'arrow-expand'} resolution="low" />
              </ButtonIcon>
            </Input>
            <Listbox
              {...listboxDownshift}
              {...restProps}
              filterFunction={filterFunction}
              listboxClass={comboboxClass.listboxClass}
              isListboxOpen={isListboxOpen}
              listboxItemRef={li => {
                listboxItemRef.current = li;
              }}
              listboxRef={ul => {
                listboxRef.current = ul;
              }}
              isOpen={isOpen}
              noMatchCallback={handleNoMatch}
              optionsTemplate={optionsTemplate}
              type={type}
            />
          </div>
        );
      }}
    </Downshift>
  );
}, preventRerender);

Combobox.defaultProps = {
  comboboxClass: {
    containerClass: null,
    inputWrapperClass: null,
    listboxClass: null
  },
  containerProps: {
    style: {
      display: 'block',
      position: 'relative'
    }
  },
  errorText: '',
  filterFunction: ComboboxUtil.filterFunction,
  inputProps: {
    label: 'Label',
    name: 'combobox'
  },
  noMatchMessage: 'No entries matching, Please try again.',
  type: 'manual'
};

Combobox.propTypes = {
  className: string,
  comboboxAction: func,
  comboboxClass: shape({
    containerClass: string,
    inputWrapperClass: string,
    listboxClass: string
  }),
  containerProps: shape({}),
  defaultItem: shape({
    value: string.isRequired
  }),
  errorText: string,
  filterFunction: func,
  inputProps: shape({
    disabled: bool,
    helpText: string,
    label: string.isRequired,
    name: string.isRequired,
    placeholder: string,
    required: bool
  }),
  noMatchMessage: string,
  onInputBlur: func,
  onInputChange: func,
  onInputFocus: func,
  onSelectionChange: func,
  optionsTemplate: shape({
    itemKey: string.isRequired,
    optionValues: arrayOf(shape({ value: string.isRequired })),
    renderOption: func.isRequired,
    searchIncludeFields: arrayOf(string)
  }).isRequired,
  showErrorText: bool,
  showHelpText: bool,
  type: oneOf(['none', 'manual', 'auto'])
};

Combobox.displayName = 'Combobox';

export default Combobox;
