/* eslint-disable no-console, spellcheck/spell-checker */

import React, { useState, useRef } from 'react';
import ButtonGroup from '../../button-group';
import ButtonText from '../../button-text';
import Col from '../../col';
import Combobox from '../../combobox';
import Grid from '../../grid';
import Row from '../../row';
import Typography from '../../typography';
import './index.css';

const ComboboxExample = () => {
  //start 1st combobox example
  const renderOptionNoneExample = optionItem => <span className="card-name">{optionItem.value}</span>;

  const inputPropsNoneExample = {
    disabled: false,
    helpText: 'Please select a card',
    label: 'Select a card',
    name: 'combobox'
  };

  const onSelectionChangeNoneExample = item => console.log(`onSelectionChange ${JSON.stringify(item)}`);

  const cardsValueNoneExample = [
    { value: 'silver signature card' },
    { value: 'visa signature card' },
    { value: 'platinum visa card' },
    { value: 'premium card' },
    { value: 'another card' },
    { value: 'other card' },
    { value: 'secondary card' },
    { value: 'tertiary card' },
    { value: 'gold signature card' },
    { value: 'yet another card' },
    { value: 'last card' }
  ];

  const optionsTemplateNoneExample = {
    itemKey: 'value',
    optionValues: cardsValueNoneExample,
    renderOption: renderOptionNoneExample,
    searchIncludeFields: ['value']
  };

  // end 1th combobox example

  // start 2st combobox example
  const [stateManualExample, setStateManualExample] = useState({
    disabled: false
  });

  const renderOptionManualExample = optionItem => <span className="card-name">{optionItem.value}</span>;

  const inputPropsManualExample = {
    disabled: stateManualExample.disabled,
    helpText : 'Please select a card',
    label: 'Select a card',
    name: 'combobox'
  };

  const onSelectionChangeManualExample = item =>{
    console.log(`onSelectionChange ${JSON.stringify(item)}`);
  };

  const cardsValueManualExample = [
    { value: 'silver signature card' },
    { value: 'visa signature card' },
    { value: 'platinum visa card' },
    { value: 'premium card' },
    { value: 'another card' },
    { value: 'other card' },
    { value: 'secondary card' },
    { value: 'tertiary card' },
    { value: 'gold signature card' },
    { value: 'yet another card' },
    { value: 'last card' }
  ];

  const optionsTemplateManualExample = {
    itemKey: 'value',
    optionValues: cardsValueManualExample,
    renderOption : renderOptionManualExample,
    searchIncludeFields: ['value']
  };
  // end 2st combobox example

  // start 3st combobox example
  const [stateAutoExample] = useState({
    disabled: false
  });

  const renderOptionAutoExample = optionItem => <span className="card-name">{optionItem.value}</span>;

  const inputPropsAutoExample = {
    disabled: stateAutoExample.disabled,
    helpText : 'Please select a card',
    label: 'Select a card',
    name: 'combobox'
  };

  const onSelectionChangeAutoExample = item =>{
    console.log(`onSelectionChange ${JSON.stringify(item)}`);
  };

  const cardsValueAutoExample = [
    { value: 'silver signature card' },
    { value: 'visa signature card' },
    { value: 'platinum visa card' },
    { value: 'premium card' },
    { value: 'another card' },
    { value: 'other card' },
    { value: 'secondary card' },
    { value: 'tertiary card' },
    { value: 'gold signature card' },
    { value: 'yet another card' },
    { value: 'last card' }
  ];

  const optionsTemplateAutoExample = {
    itemKey: 'value',
    optionValues: cardsValueAutoExample,
    renderOption : renderOptionAutoExample,
    searchIncludeFields: ['value']
  };
  // end 3st combobox example

  // start 4nd combobox example
  const comboboxClassCustomExample = {
    containerClass: 'customContainerClass',
    inputWrapperClass: 'customInputClass',
    listboxClass: 'customListBoxClass'
  };

  const countryValuesCustomExample = [
    { dialCode: 'None', shortValue: 'Empty', value: '' },
    { dialCode: '+54', shortValue: 'AR', value: 'Argentina' },
    { dialCode: '+61', shortValue: 'AU', value: 'Australia' },
    { dialCode: '+55', shortValue: 'BR', value: 'Brésil' },
    { dialCode: '+1', shortValue: 'CA', value: 'Canada' },
    { dialCode: '+56', shortValue: 'CL', value: 'Chile' },
    { dialCode: '+86', shortValue: 'CN', value: 'China' },
    { dialCode: '+57', shortValue: 'CO', value: 'Colombia' },
    { dialCode: '+1', shortValue: 'DO', value: 'République Dominicaine' },
    { dialCode: '+33', shortValue: 'FR', value: 'France' },
    { dialCode: '+350', shortValue: 'GI', value: 'Gibraltar' },
    { dialCode: '+502', shortValue: 'GT', value: 'Guatemala' },
    { dialCode: '+852', shortValue: 'HK', value: 'Hong Kong' },
    { dialCode: '+91', shortValue: 'IN', value: 'India' },
    { dialCode: '+353', shortValue: 'IE', value: 'Ireland' },
    { dialCode: '+81', shortValue: 'JP', value: 'Japan' },
    { dialCode: '+965', shortValue: 'KW', value: 'Kuwait' },
    { dialCode: '+60', shortValue: 'MY', value: 'Malaysia' },
    { dialCode: '+64', shortValue: 'NZ', value: 'Nouvelle-Zélande' },
    { dialCode: '+51', shortValue: 'PE', value: 'Peru' },
    { dialCode: '+48', shortValue: 'PL', value: 'Poland' },
    { dialCode: '+65', shortValue: 'SG', value: 'Singapore' },
    { dialCode: '+27', shortValue: 'ZA', value: 'South Africa' },
    { dialCode: '+34', shortValue: 'ES', value: 'Spain' },
    { dialCode: '+886', shortValue: 'TW', value: 'Taiwan' },
    { dialCode: '+66', shortValue: 'TH', value: 'Thailand' },
    { dialCode: '+971', shortValue: 'AE', value: 'United Arab Emirates' },
    { dialCode: '+44', shortValue: 'GB', value: 'United Kingdom' },
    { dialCode: '+1', shortValue: 'US', value: 'United States of America' }
  ];

  const renderOptionCustomExample = optionItem => (
    <React.Fragment>
      <span className="dial-code">{optionItem.dialCode}</span>
      <span className="country-name">{optionItem.value}</span>
    </React.Fragment>
  );

  const optionsTemplateCustomExample = {
    itemKey: 'shortValue',
    optionValues: countryValuesCustomExample,
    renderOption : renderOptionCustomExample,
    searchIncludeFields: ['dialCode', 'shortValue', 'value']
  };

  const onInputBlurCustomExample = () => console.log('onInputBlur');
  const onInputChangeCustomExample = () => console.log('onInputChange');
  const onInputFocusCustomExample = () => console.log('onInputFocus');
  const onSelectionChangeCustomExample = () => console.log('onSelectionChange');

  const inputPropsCustomExample = {
    disabled: false,
    label: 'Select a Country',
    name: 'combobox',
    placeholder: 'country name',
    required: true
  };
  // end 4nd combobox example

  // start 5rd combobox example
  const [stateErrorExample, setStateErrorExample] = useState({
    errorText: ''
  });

  const renderOptionErrorExample = optionItem => <span className="card-name">{optionItem.value}</span>;

  const inputPropsErrorExample = {
    disabled: false,
    helpText: 'Please select a card',
    label: 'Select a card',
    name: 'combobox'
  };

  const onSelectionChangeErrorExample = item => console.log(`onSelectionChange ${JSON.stringify(item)}`);

  const cardsValueErrorExample = [
    { value: 'silver signature card' },
    { value: 'visa signature card' },
    { value: 'platinum visa card' },
    { value: 'premium card' },
    { value: 'another card' },
    { value: 'other card' },
    { value: 'secondary card' },
    { value: 'tertiary card' },
    { value: 'gold signature card' },
    { value: 'yet another card' },
    { value: 'last card' }
  ];

  const optionsTemplateErrorExample = {
    itemKey: 'value',
    optionValues: cardsValueErrorExample,
    renderOption : renderOptionErrorExample,
    searchIncludeFields: ['value']
  };

  const dispatchComboboxActionErrorExample = useRef();

  const comboboxActionErrorExample = dispatchComboboxActionCallback => {
    dispatchComboboxActionErrorExample.current = dispatchComboboxActionCallback;
  };

  const resetErrorExample = () => {
    setStateErrorExample({ errorText: '' });
  };

  const invalidOptionErrorExample = () => {
    setStateErrorExample({
      errorText: 'Invalid option'
    });
  };
  // end 5rd combobox example

  // start 6th combobox example
  const renderOptionControlExample = optionItem => <span className="card-name">{optionItem.value}</span>;

  const inputPropsControlExample = {
    disabled: false,
    helpText : 'Please select a card',
    label: 'Select a card',
    name: 'combobox'
  };

  const onSelectionChangeControlExample = item => console.log(`onSelectionChange ${JSON.stringify(item)}`);

  const cardsValueControlExample = [
    { value: 'silver signature card' },
    { value: 'visa signature card' },
    { value: 'platinum visa card' },
    { value: 'premium card' },
    { value: 'another card' },
    { value: 'other card' },
    { value: 'secondary card' },
    { value: 'tertiary card' },
    { value: 'gold signature card' },
    { value: 'yet another card' },
    { value: 'last card' }
  ];

  const optionsTemplateControlExample = {
    itemKey: 'value',
    optionValues: cardsValueControlExample,
    renderOption : renderOptionControlExample,
    searchIncludeFields: ['value']
  };

  const dispatchComboboxActionControlExample = useRef();
  const comboboxActionControlExample = dispatchComboboxActionCallback => {
    dispatchComboboxActionControlExample.current = dispatchComboboxActionCallback;
  };

  const openMenuControlExample = () => {
    dispatchComboboxActionControlExample.current({ type:'openMenu' });
  };

  const closeMenuControlExample = () => {
    dispatchComboboxActionControlExample.current({ type:'closeMenu' });
  };

  const setHighlightedIndexControlExample = () => {
    dispatchComboboxActionControlExample.current({ type:'setHighlightedIndex' }, 2);
    dispatchComboboxActionControlExample.current({ type:'openMenu' }, 2);
  };

  const selectVisaControlExample = () => {
    dispatchComboboxActionControlExample.current({ type:'selectItem' }, { value: 'visa signature card' });
  };

  // end 6th combobox example


  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">No autocomplete</Typography>
          <Typography>The user types in any character in the
            input and this will invoke the popup menu</Typography>
          <Typography>However, the options in the menu would
            not be impacted by the characters typed in
            the textbox.</Typography>
          <Typography>The use of this option is to provide quick
            acceess to option suggestions to the user
            without limiting their entry.</Typography>
          <Typography>
            <strong>Note: Pass prop type={'none'}  will turn combobox into No autocomplete</strong>
          </Typography>
          <Typography>
            Follows <a target={"_blank"} href={"https://www.w3.org/TR/wai-aria-practices/#combobox"}>WAI-ARIA best practices</a> for implementing keyboard interaction
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Combobox
            optionsTemplate={optionsTemplateNoneExample}
            inputProps={inputPropsNoneExample}
            onSelectionChange={onSelectionChangeNoneExample}
            type={'none'}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Autocomplete with manual selection (Default)</Typography>
          <Typography>
            The user types in any character in the
            input and this will invoke the popup menu
            with filtered set of options that match with
            corresponding values.
          </Typography>
          <Typography>
            The input value that the user typed in would
            be accepted unless the user explicitly selects
            from the menu.
          </Typography>
          <Typography>
            When the user moves focus away from
            the component with Tab and no option
            from the menu has been selected, the
            input will be what the user has typed in.
            Follows WAI-ARIA best practices for implementing keyboard interaction
          </Typography>
          <Typography>
            <strong>Note: Pass prop type={'manual'} or do not set type prop will turn combobox into Autocomplete with manual selection</strong>
          </Typography>
          <Typography>
            Follows <a target={"_blank"} href={"https://www.w3.org/TR/wai-aria-practices/#combobox"}>WAI-ARIA best practices</a> for implementing keyboard interaction
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Combobox
            optionsTemplate={optionsTemplateManualExample}
            inputProps={inputPropsManualExample}
            onSelectionChange={onSelectionChangeManualExample}
          />
          <ButtonGroup direction="right" style={{ marginTop: "30px" }}>
            <ButtonText
              colorScheme="primary"
              onClick={() => setStateManualExample({ disabled: false })}
            >
              Enable
            </ButtonText>
            <ButtonText
              colorScheme="secondary"
              onClick={() => setStateManualExample({ disabled: true })}
            >
              Disable
            </ButtonText>
          </ButtonGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Autocomplete with automatic selection</Typography>
          <Typography>
            The options in the pop up menu would be
            based on the characters typed in and the
            first suggestion is automatically highlighted
            as suggested.
          </Typography>
          <Typography>
            When the user places focus back on the
            combo box, the menu should display the
            corresponding menu option to the input.
          </Typography>
          <Typography>
            <strong>Note: Pass prop type={'auto'} will turn combobox into Autocomplete with automatic selection</strong>
          </Typography>
          <Typography>
            Follows <a target={"_blank"} href={"https://www.w3.org/TR/wai-aria-practices/#combobox"}>WAI-ARIA best practices</a> for implementing keyboard interaction
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Combobox
            optionsTemplate={optionsTemplateAutoExample}
            inputProps={inputPropsAutoExample}
            onSelectionChange={onSelectionChangeAutoExample}
            type={'auto'}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Customized Listbox Option</Typography>
          <Typography>
            Combobox allows developer to use renderOption function in optionsTemplate prop to render custom listbox Option
          </Typography>
          <Typography>
            Developer can also use searchIncludeFields in optionsTemplate prop to decide which fields the autocomplete should apply to
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Combobox
            comboboxClass={comboboxClassCustomExample}
            optionsTemplate={optionsTemplateCustomExample}
            inputProps={inputPropsCustomExample}
            onInputBlur={onInputBlurCustomExample}
            onInputChange={onInputChangeCustomExample}
            onInputFocus={onInputFocusCustomExample}
            onSelectionChange={onSelectionChangeCustomExample}
            // defaultItem= {{ dialCode: '+1', shortValue: 'US', value: 'United States of America' }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Error Handling</Typography>
          <Typography>
            Combobox has default no matching message, but you can override it using noMatchMessage prop.
          </Typography>
          <Typography>
            Developer can also pass error message by using errorText prop, as long as errorText exist, the error
            will show up. So it is up to developer to add/remove errorText.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Combobox
            comboboxAction={comboboxActionErrorExample}
            optionsTemplate={optionsTemplateErrorExample}
            noMatchMessage={'There are no matches!'}
            errorText={stateErrorExample.errorText}
            inputProps={inputPropsErrorExample}
            onSelectionChange={onSelectionChangeErrorExample}
          />
          <ButtonGroup direction="right" style={{ marginTop: "30px" }}>
            <ButtonText colorScheme="primary" onClick={() => resetErrorExample()}>Clear Error</ButtonText>
            <ButtonText colorScheme="secondary" onClick={() => invalidOptionErrorExample()}>Invalid Option</ButtonText>
          </ButtonGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Programmatic control</Typography>
          <Typography>
            Combobox allows developer to programmatic trigger internal function, so developer can have more control on it.
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Combobox
            comboboxAction={comboboxActionControlExample}
            optionsTemplate={optionsTemplateControlExample}
            inputProps={inputPropsControlExample}
            onSelectionChange={onSelectionChangeControlExample}
          />
          <ButtonGroup direction="right" style={{ marginTop: "30px" }}>
            <ButtonText colorScheme="secondary" onClick={() => openMenuControlExample()}>Open listbox</ButtonText>
            <ButtonText colorScheme="secondary" onClick={() => closeMenuControlExample()}>Close listbox</ButtonText>
            <ButtonText colorScheme="secondary" onClick={() => setHighlightedIndexControlExample()}>Highlight Third Item</ButtonText>
            <ButtonText colorScheme="secondary" onClick={() => selectVisaControlExample()}>Select visa</ButtonText>
          </ButtonGroup>
        </Col>
      </Row>

    </Grid>
  );
};

export default ComboboxExample;
